import util, { parGetData, parSetData, checklogin } from '../../common/util';
export default {
	data() {
		return {
			account: '',
			password: '',
			password2: '',
			token: '',
			type: 'login'
		};
	},
	methods: {
		toggle() {
			this.type = this.type == 'login' ? 'reg' : 'login';
		},
		gopwd() {
			uni.showModal({
				title: '未绑定手机，无法找回密码',
				confirmText: '知道了',
				content: '请联系客服处理,客服10086',
				success: function(res) {}
			});
		},
		//function 1
		bindLogin() {
			if (this.account.length < 3 || this.account.length > 12) {
				uni.showToast({
					icon: 'none',
					title: '账号长度必须为3-12个字符'
				});
				return;
			}
			if (this.password.length < 3 || this.password.length > 18) {
				uni.showToast({
					icon: 'none',
					title: '密码长度必须为3-18个字符'
				});
				return;
			}
			uni.showLoading({
				title: '登录中',
				icon: 'none'
			});
			const data = {
				ddusername: this.account,
				password: this.password,
				from: util.shebei
			};
			// 去登录
			uni.request({
				url: util.apiurl + 'api/v1/login',
				method: 'POST',
				data: data,
				success: ret => {
					if (ret.statusCode == 200 && ret.data.code > 0) {
						// 存入用户信息
						uni.setStorageSync('userinfo', ret.data.data);
						// 存入token
						uni.setStorageSync('token', ret.data.token);
						uni.switchTab({
							url: '/pages/center/center'
						});
					} else {
						uni.showToast({
							title: ret.data.msg,
							icon: 'none'
						});
						setTimeout(function() {
							uni.hideLoading();
						}, 1000);
					}
				},
				complete: function() {
					uni.hideLoading();
				},
				header: {
					auth: util.httpauth
				}
			});
		},
		
		//function 2
		bindReg() {
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.password != this.password2) {
				uni.showToast({
					icon: 'none',
					title: '两次密码不同'
				});
				return;
			}
			uni.showLoading({
				title: '注册中',
				icon: 'none'
			});
			const data = {
				username: this.account,
				password: this.password,
				shebei: util.shebei
			};
			// 注册
			uni.request({
				url: util.apiurl + '404',
				method: 'POST',
				data: data,
				success: ret => {
					if (ret.statusCode == 200 && ret.data.code < 1) {
						// 存入用户信息
						uni.setStorageSync('userinfo', ret.data.data);
						// 存入token
						uni.setStorageSync('token', ret.data.token);
						uni.switchTab({
							url: '/pages/center/center'
						});
					} else {
						uni.showToast({
							title: ret.data.msg,
							icon: 'none'
						});
						setTimeout(function() {
							uni.hideLoading();
						}, 1000);
					}
				},
				complete: function() {
					uni.hideLoading();
				},
				header: {
					auth: util.httpauth
				}
			});
		}
	}
	
};
